import React from 'react';
import { Layout, Tabs } from "antd";
import HeadBarView from "../../components/HeadBar/index";
import ContentView from "./Content/ContentView";
import PannelView from "./PannelView/index";
import ListView from './ListView/ListView'
import "./Homepage.less";

const { Header,  Content } = Layout;
const { TabPane } = Tabs;


export default class Homepage extends React.Component {  

    render() {

        return (
            <Layout style={{position: 'relative', minHeight:document.documentElement.clientHeight}}>
                <Header className='home-header' style={{ height: "50px" }}>
                    <HeadBarView {...this.props} />
                </Header>
                <Content className="pageWrapper">
                    <ContentView {...this.props}>
                        <Tabs defaultActiveKey='1' >
                            <TabPane key='1' tab="数据快报">
                                <ListView {...this.props}></ListView>
                            </TabPane>
                            <TabPane key='2' tab="数据上传">
                                <PannelView {...this.props}></PannelView>
                            </TabPane>
                        </Tabs>
                    </ContentView>
                </Content>

                <div className='bottomTip'>
                    <div className='logo'></div>
                    <span>Powered By Calliope</span>
                </div>
            </Layout >
        )
    }
}